/* width */

::-webkit-scrollbar {
    width:5px;
    background-color: #333;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    width:5px;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    height: 10px;
    background: grey;
   
  }
.noteListContainer{
    min-width:  300px;
    height: 100vh;
    background-color: #333;
}

.addNoteBtn{
    margin-left: 5px;
    margin-top: -13px;
    position: absolute;
    width: 25px;
    height: 25px;
    transition: 2s;
    border: none;
    background-color: transparent;
    font-size: 50px;
    color: rgb(0, 255, 0);
    transition: .1s;
}
.addNoteBtn:hover {
color: rgb(0, 133, 0);
}

.addNoteBtn i{
  margin-left: -2px;
}
.addNoteBtn:active{
    transform: scale(1.1);
    color: rgb(0, 133, 0);
}

.sideBarBtn i{
  margin-left: -2px;
}

.sideBarBtn{
    width: 25px;
    height: 100vh;
    transition: 2s;
    border: none;
    background-color: transparent;
    font-size: 25px;
}

.noteForm{
  padding-right: 10px;
  height: 98.35vh;
  overflow: auto;
}

.noteTitle{
  width:200px;
  background-color: transparent;
  border: none;
  font-size: 1.25rem;
  margin-bottom: 0.75rem;
  margin-top:34px;
  text-align:center;
}


.addActivityBtn{
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}


.addActivityBtn i{

  font-size: 35px;
  color: rgb(0, 255, 0);
}

.addActivityBtn i:hover{

  cursor: pointer;
  color: rgb(0, 133, 0);
}


.addActivityBtnContainer i{
  font-size: 25px;
 
}

.addActivityBtnContainer .fa-check-square{
  color: rgb(0, 255, 0);
  
}

.fa-minus-square{
  color: red;
}

.addActivityBtnContainer i:hover{
  cursor: pointer;
  filter: sepia(50%);
}

.deleteActivityBtn{
  font-size: 25px;
  color: red;
}

.deleteActivityBtn:hover{
  cursor: pointer;
  filter: sepia(50%);
}


